@import '/Users/yesdoing/Documents/Instagram/frontend/src/config/_variables.scss';
.form-component {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
    margin-bottom: 25px;
    .form {
      display: flex;
      flex-direction: column;
      .button {
        margin-top: 10px;
      }
      .text-input {
        border: 1px solid #efefef;
        border-radius: 3px;
        background-color: $bg-color;
        margin-bottom: 5px;
        font-size: $nomal-font-size;
        padding: 9px 0 7px 8px;
        font-weight: 400;
        &:focus {
          outline: none;
          border: 1px solid #b2b2b2;
        }
      }
    }
    .divider {
        text-transform: uppercase;
        color: $dark-grey;
        font-weight: 600;
        margin: 20px 0;
        margin-bottom: 20px;
        position: relative;
      &:before {
        content: "";
        width: 42%;
        position: absolute;
        background-color: $dark-grey;
        height: 1px;
        left: 0;
        top: 6px;
        opacity: 0.8;
      }
      &:after {
        content: "";
        width: 42%;
        position: absolute;
        background-color: $dark-grey;
        height: 1px;
        right: 0;
        top: 6px;
        opacity: 0.8;
      }
    }
    .facebook-login {
        align-items: center;
        font-size: $nomal-font-size;
        border: 0;
        color: #385185;
        justify-content: center;
        margin-bottom: 25px;
        i {
            margin-right: 5px;
        }
        &:focus,
        &:active {
          outline: none;
        }
    }
    .forget-link {
        font-size: $small-font-size;
        color: $dark-blue;
    }
    .button {

      @include blueButton();
      i {
        margin-right: 10px;
      }
    }
    .terms {
      width: 80%;
      margin: 0 auto;
      margin-top: 20px;
      line-height: 20px;
      color: $dark-grey;
      span {
        font-weight: 600;
        cursor: pointer;
      }
    }
  }
  
  .error {
    color: red;
    font-weight: 600;
    margin-top: 15px;
  }
