@import '/Users/yesdoing/Documents/Instagram/frontend/src/config/_variables.scss';
.container {
    .spinner {
        height: 32px;
        animation: spinner 1.5s steps(16) infinite;
    }
}

@keyframes spinner {
    from {
        transform: rotate(0deg)
    }
    to {
        transform: rotate(360deg)
    }
}